*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    background-color:rgba(26, 2, 2, 0.952);
    color: honeydew;
}
.App{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin-left: 30px;
    color: #fff;
}
.coinSearch{
    margin-bottom: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.coinText{
    margin: auto;
    padding: auto;
    cursor: pointer;

}

.coinInput{
    padding-left: 20px;
    width: 300px;
    height: 50px;
    border-radius: 10px;
    border: none;
    color: #e2e2e2;
}
.coinInput::placeholder{
    color: #e2e2e2;
}
