.coinsContainer{
    display: flex;
    justify-content: center;
}
.coinRow{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    width: 900px;
    border-bottom: 1px solid #c5c5c5;
}
.coin{
    display: flex;
    align-items: center;
    padding-right: 30px;
    min-width: 300px;
}
.coin h1{
    font-size: 18px;
    width: 150px;
}
.coin img{
    height: 30px;
    margin-right: 30px;
    width: 30px;

}
.coinSymbol{
    text-transform: uppercase;
}
.coinData{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%
}
.coinPrice{
    width: 110px;
}
.coinVolume{
    width: 230px;
}
.coinMarket{
    width: 150px;

}
.coinPercent{
    width: 100%;
}
.red{
    color: red;
    font-size: 20px;
}
.green{
    color: green;
    font-size: 20px;
}
